
.container-1 {
    background-color: #ff4b33;
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
}

.header-text {
    text-align: center;
    font-size: 66px;
    margin-top: 0px;
    margin-bottom: 10px;
    color: white;
}

.nav {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 20px;

}

.link {
    text-align: center;
    flex: 1 1 0;
    text-decoration: none;
    color: white;
    font-weight: bold;
}

.link:hover {
    color: rgb(60,60,60)
}